@import '~antd/dist/antd.css'; /* ¡¡¡ HERE THE SOLUTION !!! */

*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    /*anything that we do here is going to show up on the background behind the canvas element*/
    height: 100vh;
    font-family: 'Poppins';
    background: rgb(24, 24, 24);
}

body {
    overflow-x: hidden;
}

.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    mix-blend-mode: exclusion;
}

.container
{
    height: 100vh;
    display: grid;
    place-items: center;
    
}

h1 
{
    font-size: 8.19rem;
    text-transform: uppercase;
    color: white;
    font-weight: bold;
    margin-bottom: 0;
    margin-top: 1.028em;

}

section{height: 100vh;}

#input{
    margin-top: 0;
    margin-bottom: 25em;
}